/* *{
  transition: all 0.3s;
} */
body,html{
  background: #EEEEEE;
  margin: 0;
  padding: 0;
}
#root{
  display: flex;
  justify-content: center;
  min-height: 100vh;
  background-color: #fff;
}
.Inputstyle{
    min-width: 300px;
    height: 32px;
    border: 1px solid #ddd;
    border-radius: 5px;
}
.InputForm{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}
@media (max-width:768px){
    .Inputstyle{
        min-width: 268px;
    }
  }
.TodoItem{
    display: flex;
    align-items: center;
}
p{
  word-wrap:break-word;
}
@media (max-width:768px){
  .TodoItem{
    width: 168px;
  }
}
.changeColor{
    text-decoration:line-through;
}
.text{
    width: 100%;
    text-align: center;
    margin: 6px;
}
.UserDialog-Wrapper{ 
    /*全屏*/      position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
    /*内容居中*/  display: flex; justify-content: center; align-items: center;
    background: hsla(0,0%,0%,0.40);
  }
  .UserDialog{
    border-radius: 4px;
    min-width: 23em;
    max-width: 30em;
    background: white;
    border: 1px solid #aaa;
    box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
  }
  .UserDialog > nav:first-child{ padding: 1.2em; margin-left: -2em;}
  .UserDialog .signInOrSignUp > nav:first-child{ padding: 1em; margin-left: -2em;}
.UserDialog .signInOrSignUp > nav:first-child label{ margin-left: 2em; cursor: pointer;}
.UserDialog .signInOrSignUp > .panes { padding: 0 1em 1em; }

.UserDialog .forgotPassword > h3 { padding: 1em; }
.UserDialog .forgotPassword > form >.row.actions{ padding-left: 0; display: flex; align-items: center;}
/* .UserDialog .forgotPassword > form >.row.actions>button{min-width: 21.5em;} */
.UserDialog .forgotPassword > form >.row{ display: flex; padding: .6em 0;margin: 0 .6em; }
  .UserDialog form>.row{ display: flex; padding: .6em 0; }
  .UserDialog form>.row>label:first-child{ width: 4em; }
  .UserDialog form>.row.actions{ padding-left: 0; display: flex; align-items: center;}
  .UserDialog form>.row.actions>button{min-width: 21.5em;}
  @media (max-width:768px){
    .App{
      min-width: 18em;
      margin: 0;
    }
  }
.App {
  color: #333;
  border: none;
  border-radius: 6px;
  background: white;
  margin: 2em;
  padding: 0 2em;
  min-height: 20em;
  min-width: 32em;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}
.App > .plus{
  font-size: 30px;
  color:#fff;
  background-color: #19DB7A;
  padding: 8px 22px;
  border-radius: 6px;
  width: 66px;
  margin: 20px 0 20px;
  display: inline-block;
}
.App > .plus:hover{
  cursor: pointer;
  box-shadow: 5px 5px 22px -5px rgba(0,0,0,0.25);
}
.App .todoList{
  margin: 20px 0;
  max-width: 390px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.App .todoList li{
  margin-bottom: 10px; 
}
.App .todoList li:hover{
  box-shadow: 5px 5px 22px -5px rgba(0,0,0,0.25);
}
.App > .logo{
  text-align: center;
  padding: 36px 10px;
}
.App > .logo > span{
  font-size: 44px;
  font-weight: 600;
}
.App > .logo > span >.spanStyle{
  font-weight: 300;
  color: #B0A3A7;
}
.App > h2{
  display: inline-block;
}
.App > .SignOutBtn{
  float: right;
}
.App > .ant-layout-footer{
  text-align: center;
}
.App > .ant-layout-footer > a{
  margin: 0 6px;
}
@media (max-width:768px){
  .App{
    min-width: 26.6em;
    margin: 0;
    padding: 0 1em;
  }
  .App .todoList{
    margin: 20px 0 20px;
  }
  .App > .SignOutBtn{
    float: none;
    margin-left: 20px;
  }
  .App .ant-layout-footer{
    height: 69px;
  }
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  *::after{
    box-sizing: border-box;
  }
  *::before{
    box-sizing: border-box;
  }
  ul,ol,dl{
    list-style:none;
  }
  a{
    color: inherit;
  }
